<template>
    <div class="demo-treelist">
        <ui-list>
            <ui-listitem disabled
                         to="/"></ui-listitem>
            <ui-listitem to="/icon"></ui-listitem>
            <ui-divider></ui-divider>
            <ui-sub-header></ui-sub-header>
            <ui-treeitem :tree="treeRoutes" :expandAll="false" :expandedKeys="['111']" accordion>
                <template slot-scope="{ node }">
                    <i v-if="node.icon"
                       :class="`icon-${node.icon}`"></i> {{node.text}}
                </template>
            </ui-treeitem>
        </ui-list>
    </div>
</template>
<script>
export default {
    data () {
        return {
            treeRoutes: {
                id: '000',
                text: '',
                icon: 'sitemap',
                children: [{
                    id: '111',
                    text: '',
                    icon: 'code',
                    children: [{
                        id: '1111',
                        text: ''
                    }, {
                        id: '1112',
                        text: ''
                    }, {
                        id: '1113',
                        text: ''
                    }, {
                        id: '1114',
                        text: ''
                    }]
                }, {
                    id: '222',
                    text: '',
                    icon: 'tasks',
                    children: [{
                        id: '2221',
                        text: '1'
                    }, {
                        id: '2222',
                        text: '2'
                    }]
                }, {
                    id: '333',
                    text: '',
                    icon: 'user-circle',
                    children: [{
                        id: '3331',
                        text: '1'
                    }, {
                        id: '3332',
                        text: '2'
                    }]
                }]
            }
        }
    }
}
</script>
<style>
.demo-treelist {
    max-width: 300px;
    border: 1px solid #ddd;
}
</style>



