<template>
    <docs-page>
        <code-block title=""
                    :code="baseCode"
                    desc="">
            <baseDemo></baseDemo>
        </code-block>
        <code-block title=""
                    :code="selectorCode"
                    desc="`value`">
            <selectorDemo></selectorDemo>
        </code-block>
    </docs-page>
</template>
<script>
import baseDemo from './base'
import baseCode from '!raw-loader!./base'
import selectorDemo from './selector'
import selectorCode from '!raw-loader!./selector'

export default {
    components: {
        baseDemo,
        selectorDemo
    },
    data() {
        return {
            baseCode,
            selectorCode
        }
    }
}
</script>




